<template>
<div class="page login">
    <header id="header" class="mui-bar mui-bar-nav">
        <h1 class="mui-title"></h1>
        <!-- <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a> -->
    </header>
    <div class="mui-scroll-wrapper" style="bottom: 0px;">
    <div class="mui-scroll">
    <img class="login-tip" :src="'images/login/login-tip.png'" />
    <a class="btn-check" :class="{'active': identity==1}" @click="identity = 1"></a>
    <a class="btn-check" :class="{'active': identity==2}" @click="identity = 2" ></a>
    <a class="btn-check" :class="{'active': identity==3}" @click="identity = 3"></a>
    <a class="btn-check" :class="{'active': identity==4}" @click="identity = 4"></a>
    <p class="text"></p>
    <p class="password" v-if="identity!=0 && identity!=1"><input type="text" v-model="password"><br></p>
    <p class="number" v-if="identity==2"><input type="text" v-model="number"><br></p>
    <button type="button" class="mui-btn mui-btn-warning mui-btn-block" v-if="btnLogin" @click="login"></button>
    <button type="button" class="mui-btn mui-btn-outlined mui-btn-block" v-if="!btnLogin"></button>
    <p class="text gay"></p>
    </div></div>
</div>
</template>

<script>
export default{
    name: 'login',
    data: function(){
        return{
            identity: 0,
            btnLogin: false,
            password: '',
            number: ''
        }
    },
    ready: function(){
        document.title = ''
        mui('.mui-scroll-wrapper').scroll({bounce: false})
    },
    watch: {
        identity: function(){
            this.isLogin()
        },
        password: function(){
            this.isLogin()
        },
        number: function(){
            this.isLogin()
        }
    },
    methods: {
        isLogin(){
            if(this.identity == 1) this.btnLogin = true
            else if(this.identity == 2){
                if(this.password == '' || this.number == '') this.btnLogin = false
                else this.btnLogin = true
            }else{
                if(this.password == '') this.btnLogin = false
                else this.btnLogin = true
            }
        },
        login(){
            let _data = {
                identity: this.identity,
                storeid: this.$route.params.storeid,
                code: this.password,
                num: this.number
            }
            this.$store.state.showTip({type: 'loading', content: '...' })
            this.$http.post(API.choiceIdentity, _data).then( (data) => {
                if(data.data.errorInfo) return
                this.$store.state.hideTip()
                // 
                if(this.identity == 1){
                    this.$router.go('/consumer/' + this.$route.params.token + '/' + this.$route.params.storeid)
                    return
                }
                if(this.identity == 2){
                    this.$router.go('/tech/' + this.$route.params.token)
                    return
                }
                // 
                if(this.identity == 3 || this.identity == 4){
                    this.$router.go('/manage-shop/' + this.$route.params.token)
                    return
                }
            })
        }
    }
}
    
</script>


