s=\"status center add\" v-if=\"item.apply_state!=0\" @click=\"operate(item.item_id, 'add')\"></i>\n                    <i class=\"status center fail\" v-if=\"item.apply_state==0\"></i>\n                </a>\n            </li>\n            <div class=\"no-data\" v-if=\"getAddProject.length<=0\">\n                <h6>~</h6>\n            </div>\n        </ul>\n\n        </div></div>\n    \n</div>\n";


