import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    URL: 'http://www.yangshenghui.net/',
    tip: {
        show: false,
        options: {}
    },
    showTip: function(opt){
        this.tip.show = true
        this.tip.options = opt
    },
    hideTip: function(){
        this.tip.show = false
    },
    storeLabel: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
    ],
    tecLabel: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
    ],
    __getImgData: function(img, dir, next) {
        let image = new Image();
        image.onload = function(){
            let degree = 0,drawWidth,drawHeight,width,height;
            drawWidth = this.naturalWidth;
            drawHeight = this.naturalHeight;
            if(drawWidth > 2000 || drawHeight > 2000){
                drawWidth = drawWidth / 3;
                drawHeight = drawHeight / 3;
            }
            let canvas = document.createElement('canvas');
            canvas.width = width = drawWidth;
            canvas.height = height = drawHeight; 
            let context = canvas.getContext('2d');
            let isRouter = true;
            //canvasiphonehome
            switch(dir){
                //iphonehome
                case 3:
                    degree = 180;
                    drawWidth = -width;
                    drawHeight = -height;
                    break;
                //iphonehome()
                case 6:
                    canvas.width = height;
                    canvas.height = width; 
                    degree = 90;
                    drawWidth = width;
                    drawHeight = -height;
                    break;
                //iphonehome
                case 8:
                    canvas.width = height;
                    canvas.height = width; 
                    degree = 270;
                    drawWidth = -width;
                    drawHeight = height;
                    break;
            }
            //canvas
            context.rotate(degree * Math.PI / 180);
            context.drawImage(this, 0 , 0, drawWidth, drawHeight);
            next(canvas.toDataURL(0.7));
        }
        image.src = img;
    },
    locationReload: function(){ location.reload() },
    pageSize: 10,
}

export default new Vuex.Store({ state })


