module.exports = "\n<div class=\"page consumer\">\n    <header id=\"header\" class=\"mui-bar mui-bar-nav\">\n        <h1 class=\"mui-title\"></h1>\n        <!-- <a class=\"mui-action-back mui-icon mui-icon-left-nav mui-pull-left\"></a> -->\n    </header>\n    \n    <div class=\"mui-scroll-wrapper\">\n    <div class=\"mui-scroll\">\n    <div id=\"sliderSegmentedControl\" class=\"mui-slider-indicator mui-segmented-control mui-segmented-control-inverted\">\n        <a class=\"winslow-control-item mui-active\" ></a>\n        <a class=\"winslow-control-item\" v-link=\"{path: '/consumer-my/' + $route.params.token + '/' + $route.params.storeid}\"></a>\n    </div>\n\n    <ul class=\"order-container\">\n        <li v-for=\"item in userOrderlist\">\n            <div class=\"order-text\">\n                <div class=\"image-size-list\" style=\"background-image: url({{$store.state.URL + item.item_image}});\"></div>\n                <!-- <img :src=\"$store.state.URL + item.item_image\"> -->\n                <h1 style=\"width: 80%;\">{{item.item_name}}</h1>\n                <h6>{{item.order_createtime | format 'yyyy-MM-dd hh:mm'}}</h6>\n                <p>{{item.order_totalprice}}</p>\n                <div class=\"price\">{{item.order_totalprice}}<br>\n                    <em class=\"suc\" v-if=\"item.order_status == 1 && item.order_pay == 1\"></em>\n                    <em class=\"cancel\" v-if=\"item.order_status == 2\"></em>\n                    <em v-if=\"item.order_status == 3\"></em>\n                    <em class=\"fin\" v-if=\"item.order_status == 4\"></em>\n                    <em class=\"fin\" v-if=\"item.order_status == 6\"></em>\n                    <em class=\"fin\" v-if=\"item.order_status == 1 && item.order_pay == 0\"></em>\n                </div>\n            </div>\n            <div class=\"order-btn\">\n                <a v-if=\"item.order_status != 1 && item.order_status != 2\" @click=\"chooseGood(item.order_id)\"></a>\n                <a v-if=\"item.order_status == 3\" @click=\"cancelOrder(item.order_id)\"></a>\n                <a v-link=\"{path: '/consumer-order-info/' + this.$route.params.token + '/' + this.$route.params.storeid + '/' + item.order_id}\"></a>\n                <a class=\"red\" v-if=\"item.order_status == 1 && item.order_pay == 0\" v-link=\"{path: '/consumer-pay/' + this.$route.params.token + '/' + this.$route.params.storeid + '/' + item.order_id}\"></a>\n                <a class=\"red\" v-if=\"item.order_status == 6\" @click=\"$store.state.showTip({type: 'error', content: '' })\"></a>\n                <a class=\"red\" v-if=\"item.order_status == 1 && !item.order_comment && item.order_pay == 1\" v-link=\"{path: '/consumer-post-comment/' + this.$route.params.token + '/' + this.$route.params.storeid + '/' + item.order_id}\"></a>\n                <a class=\"red\" v-if=\"item.order_status == 1 && item.comment && item.order_pay == 1\"></a>\n                <a class=\"red\" v-if=\"item.order_status == 2\" @click=\"delOrder(item.order_id)\"></a>\n            </div>\n        </li>\n    </ul>\n\n    <div class=\"no-data\" v-if=\"userOrderlist.length<=0\">\n        <h1></h1>\n        <h6></h6>\n        <a v-link=\"{path: '/consumer/' + this.$route.params.token + '/' + this.$route.params.storeid}\"></a>\n    </div>\n\n    <a class=\"btn-more\" v-if=\"btnPage\" @click=\"nextPage\"></a>\n\n    </div>\n    </div>\n\n    <nav class=\"mui-bar mui-bar-tab\">\n        <a class=\"winslow-tab-item\" v-link=\"{path: '/consumer/' + this.$route.params.token + '/' + this.$route.params.storeid}\">\n            <span class=\"iconfont\">&#xe67d;</span>\n            <span class=\"mui-tab-label\"></span>\n        </a>\n        <a class=\"winslow-tab-item mui-active\">\n            <span class=\"iconfont\">&#xe61e;</span>\n            <span class=\"mui-tab-label\"></span>\n        </a>\n    </nav>\n\n    <div class=\"choose-good\" v-show=\"goodShow\">\n        <div class=\"container\">\n            <h1></h1>\n            <ul class=\"wrap\">\n                <li v-for=\"item in goodsList\" data-id=\"{{item.goods_id}}\">\n                    <img src=\"{{$store.state.URL+item.goods_head}}\">\n                    <span class=\"name\">{{item.goods_name}}</span>\n                    <span class=\"price\">{{item.goods_price}}</span>\n                    <div class=\"mui-numbox\">\n                      <button class=\"mui-btn mui-numbox-btn-minus\" type=\"button\">-</button>\n                      <input class=\"mui-numbox-input\" type=\"number\" value=\"{{item.value}}\" />\n                      <button class=\"mui-btn mui-numbox-btn-plus\" type=\"button\">+</button>\n                    </div>\n                </li>\n                <div v-if=\"goodsList.length<=0\" class=\"no-data\"><h6></h6></div>\n            </ul>\n            <footer>\n                <a @click=\"goodShow = false\"></a><a @click=\"subGood()\"></a>\n            </footer>\n        </div>\n    </div>\n    \n    \n</div>\n";


