<template>
<div class="page manage">
    <header id="header" class="mui-bar mui-bar-nav haed2 ">
        <h1 class="mui-title"></h1>
        <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
    </header>
    <div class="mui-scroll-wrapper" style="bottom: px;">
    <div class="mui-scroll">
    <div class="my-info-container no-border">
        <div class="my-info">
            <img :src="techInfo.massagist_photo" class="head">
            <div class="info mui-ellipsis">
                <h1>{{techInfo.massagist_no}}</h1>
                <h2>{{techInfo.massagist_birth || '' | years}}</h2>
                <h3><start :value='parseInt(techInfo.massagist_store)'></start> {{parseInt(techInfo.massagist_store)}}</h3>
            </div>
            <div class="info">
                <h1>{{techInfo.massagist_name}}</h1>
                <h2>{{techInfo.c_name}}</h2>
            </div>
        </div>
    </div>

    <div class="title" @click="showChange=!showChange"><i class="ico-down {{!showChange?'up':''}}"></i>({{getHandleProject.length}})</div>
        <ul class="mui-table-view item-container" v-show="showChange">
            <li class="mui-table-view-cell mui-media" v-for="item in getHandleProject">
                <a href="javascript:;">
                    <img class="mui-media-object mui-pull-left" :src="$store.state.URL + item.item_image">
                    <div class="mui-media-body">
                        {{item.item_name}}
                        <p class="mui-ellipsis">{{(item.apply_time || '') | format 'yyyy-MM-dd hh:mm'}}</p>
                    </div>
                    <i class="status-text">{{item.handle == 'add' ? '' : ''}}</i>
                    <i class="status agree" v-if="item.apply_state == 1"></i>
                    <i class="status reject" v-if="item.apply_state == 2"></i>
                    <i class="status true" v-if="item.apply_state == 0" @click="operate(item.item_id, 'agree')"></i>
                    <i class="status false" v-if="item.apply_state == 0" @click="operate(item.item_id, 'refuse')"></i>
                </a>
            </li>
            <div class="no-data" v-if="getHandleProject.length<=0">
                <h6>~</h6>
            </div>
        </ul>

        <div class="title" @click="showHave=!showHave"><i class="ico-down {{!showHave?'up':''}}"></i>({{getHaveProject.length}})</div>
        <ul class="mui-table-view item-container" v-show="showHave">
            <li class="mui-table-view-cell mui-media" v-for="item in getHaveProject" >
                <a href="javascript:;">
                    <img class="mui-media-object mui-pull-left" :src="$store.state.URL + item.item_image">
                    <div class="mui-media-body center">
                        {{item.item_name}}
                    </div>
                    <i class="status center remove" v-if="item.apply_state!=0" @click="operate(item.item_id, 'delete')"></i>
                    <i class="status center fail" v-if="item.apply_state==0"></i>
                </a>
            </li>
            <div class="no-data" v-if="getHaveProject.length<=0">
                <h6>~</h6>
            </div>
        </ul>

        <div class="title" @click="showProject=!showProject"><i class="ico-down {{!showProject?'up':''}}"></i>({{getAddProject.length}})</div>
        <ul class="mui-table-view item-container" v-show="showProject">
            <li class="mui-table-view-cell mui-media" v-for="item in getAddProject" >
                <a href="javascript:;">
                    <img class="mui-media-object mui-pull-left" :src="$store.state.URL + item.item_image">
                    <div class="mui-media-body center">
                        {{item.item_name}}
                    </div>
                    <i class="status center add" v-if="item.apply_state!=0" @click="operate(item.item_id, 'add')"></i>
                    <i class="status center fail" v-if="item.apply_state==0"></i>
                </a>
            </li>
            <div class="no-data" v-if="getAddProject.length<=0">
                <h6>~</h6>
            </div>
        </ul>

        </div></div>
    
</div>
</template>

<script>
import Start from '../../components/start.vue'
export default{
    name: 'manageTechInfo',
    data: function(){
        return{
            getHaveProject: [],
            getHandleProject: [],
            getAddProject: [],
            showChange: true,
            showHave: true,
            showProject: true,
            techInfo: {}
        }
    },
    ready: function(){
        document.title = ''
        this.$store.state.showTip({type: 'loading', content: '...' })
        mui('.mui-scroll-wrapper').scroll({bounce: false})
        this.$http.post(API.getMassagistInfo, {tid: this.$route.params.id}).then( (data) => {
            if(data.data.errorInfo) return
            this.$store.state.hideTip()
            this.techInfo = data.data.object
            if(this.techInfo.massagist_photo.indexOf('http')<0){
                this.techInfo.massagist_photo = this.$store.state.URL + this.techInfo.massagist_photo
            }
        })
        this.getProject()
    },
    methods: {
        local: function(name){
            this.$router.go('/'+ name +'/' + this.$route.params.token)
        },
        getProject(){
            // 
            this.$http.post(API.getHaveProject, {tid: this.$route.params.id}).then( (data) => {
                if(data.data.errorInfo) return
                this.$store.state.hideTip()
                this.getHaveProject = data.data.list
            })
            // 
            this.$http.post(API.getHandleProject, {tid: this.$route.params.id}).then( (data) => {
                if(data.data.errorInfo) return
                this.$store.state.hideTip()
                this.getHandleProject = data.data.list
            })
            // 
            this.$http.post(API.getAddProject, {tid: this.$route.params.id}).then( (data) => {
                if(data.data.errorInfo) return
                this.$store.state.hideTip()
                this.getAddProject = data.data.list
            })
        },
        operate(pid, action){
            let alertText = ''
            let me = this
            if(action == 'add') alertText = ''
            if(action == 'delete') alertText = ''
            if(action == 'agree') alertText = ''
            if(action == 'refuse') alertText = ''
            mui.confirm(''+alertText+'', '', ['', ''], function(e) {
                if (e.index == 1) {
                    me.$store.state.showTip({type: 'loading', content: alertText + '...' })
                    me.$http.post(API.applyProject, {
                        pid: pid,
                        tid: me.$route.params.id,
                        action: action
                    }).then( (data) => {
                        if(data.data.errorInfo) return
                        me.$store.state.hideTip()
                        me.$store.state.showTip({type: 'success', content: '' })
                        me.getProject()
                    })
                }
            })
        }
    },
    components: {
        Start
    }
}
    
</script>


